body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

div#root {
  width: 90%;
  margin: 0 auto;
}

div.main_table {
  padding: 20px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

.one_ticket {
  border: 1px solid red;
  display: -webkit-flex;
  display: flex;
  margin-bottom: 10px;
  width: 45%;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

.one_ticket div {
  border: 2px solid blue;
  /* flex-basis: 15%; */
  text-align: center;
  width: 30px;
  height: 30px;
  line-height: 30px;
  border-radius: 50%;
  padding: 10px;
  margin: 5px;
}

.full_number {
  background-color: yellow;
}
header {

    margin: 0 auto;
    text-align: center;
    background-color: #aaa;
}
.navigation {
    background-color: #acd;
}

nav button {
    padding: 10px 20px;
    margin: 0 auto;
    display: block;
}
